  display: flex;
  border-top: 0;
}
.vux-loadmore.weui-loadmore_line:before,
.vux-loadmore.weui-loadmore_line:after {
  position: relative;
  top: -1px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  content: '';
  border-top: 1px solid #E5E5E5;
}

.vux-divider {
  display: table;
  white-space: nowrap;
  height: auto;
  overflow: hidden;
  line-height: 1;
  text-align: center;
  padding: 10px 0;
  color: #666;
}
.vux-divider:after,.vux-divider:before {
  content: '';
  display: table-cell;
  position: relative;
  top: 50%;
  width: 50%;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,)
}
.vux-divider:before {
  background-position: right 1em top 50%
}
.vux-divider:after {
  background-position: left 1em top 50%
}

