@charset 'UTF-8';

/* CSS Reset */

* {
	margin: 0;
	padding: 0;

	outline: 0;

	-webkit-tap-highlight-color: transparent;
}

*, *::before, *::after {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

html {
	font-family: sans-serif;

	height: 100%;

	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;

	-webkit-text-size-adjust: 100%;
	    -ms-text-size-adjust: 100%;
}

body {
	font-family: -apple-system, Arial, Helvetica, \534E\6587\7EC6\9ED1, sans-serif;
	/* height: 100%; */
	/* line-height: 1; */
	line-height: normal;
	background: #eff7f7;
}

body,#app{
	height: 100%;
}

h1, h2, h3, h4, h5, h6 {
	font-family: inherit;
	font-size: inherit;
	font-weight: 400;
}

ul, ol {
	list-style: none;
}

table {
	border-spacing: 0;
	border-collapse: collapse;
}

input, textarea, button, select {
	font-family: inherit;
	font-size: inherit;
	font-weight: 400;

	border: 0;
	border-radius: 0;
	background-color: transparent;

	-webkit-appearance: none;
}

input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
	-webkit-appearance: none;
}

input[type='number'] {
	-moz-appearance: textfield;
}

input[type='checkbox'] {
	-webkit-appearance: checkbox;
}

input[type='radio'] {
	-webkit-appearance: radio;
}

input[type='button'] {
	-webkit-appearance: button;
}

/* 去除input [type=search] 的删除按钮 */

input[type='search']::-webkit-search-decoration, input[type='search']::-webkit-search-cancel-button {
	-webkit-appearance: none;
}

/* 去除记住密码后自动填充表单的黄色背景 */

input:-webkit-autofill {
	font-family: inherit;
	font-size: inherit;
	font-weight: 400;

	background-color: transparent;
	background-image: transparent;
	-webkit-box-shadow: 0 0 0 1000px white inset;
}

textarea {
	resize: none;
}

img {
	vertical-align: middle;

	border: 0;
}

a, a:active, a:hover {
	cursor: pointer;
	text-decoration: none;
}

b, i, em {
	font-weight: normal;
	font-style: normal;
}

pre {
	font-family: inherit;
	font-size: inherit;

	white-space: pre-wrap;
	word-wrap: break-word;
}

/* 隐藏默认视频播放控件样式 */

video::-webkit-media-controls, video::-moz-media-controls, video::-webkit-media-controls-enclosure {
	display: none !important;
}

video::-webkit-media-controls-panel, video::-webkit-media-controls-panel-container, video::-webkit-media-controls-start-playback-button {
	display: none !important;

	-webkit-appearance: none;
}

video::-webkit-media-controls {
	opacity: 0;
}

/* 清除浮动 */

.clearfix::after, .wrap::after {
	font-size: 0;
	line-height: 0;

	display: block;

	height: 0;

	content: ' ';
}

.clearfix {
	clear: both;
}

